<template>
    <div class="scan">
      <div class="contain">
        <img src="../assets/scan.png" alt="">
        <p class="title"></p>
      </div>
      <div class="scanBtn" @click="scan">
        <img src="../assets/scanicon.png" alt="">
        <span></span>
      </div>
    </div>
</template>
<script>
import wx from "weixin-js-sdk";
import scanAPI from "@/pages/index/services/scan";
document.title = "";
export default {
  data() {
    return {};
  },
  methods: {
    scan: function() {
      const params = {
        url: window.location.href,
        company_id: window.localStorage.getItem("company_id")
      };
      const _this = this;
      scanAPI.wxSdkConfig({ data: params }).then(res => {
        const { data } = res.data;
        wx.config({
          beta: true, // wx.invokejsapi
          // ,apialertpclogpc
          // debug: true,
          // 
          appId: res.data.appid,
          // 
          timestamp: data.timestamp,
          // 
          nonceStr: data.nonceStr,
          // 
          signature: data.signature,
          // JSJS
          jsApiList: ["scanQRCode"]
        });
      });

      wx.ready(function() {
        wx.scanQRCode({
          desc: "scanQRCode desc",
          needResult: 1, // 01
          scanType: ["qrCode"], // 
          success: function(res) {
            var result = res.resultStr; // needResult  1 
            var a = result.split("/");
            _this.$router.push({
              name: "scanResult",
              params: { code: a[a.length - 1] }
            });
          }
        });
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.scan {
  .contain {
    display: flex;
    display: -webkit-flex; /* Safari */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      width: 377px;
      height: 377px;
      margin: 48px 0;
    }
    .title {
      font-size: 30px;
      color: rgba(114, 113, 113, 1);
    }
  }
  .scanBtn {
    width: 400px;
    height: 88px;
    margin: 60px auto;
    background: #51b8cb;
    border-radius: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 52px;
      height: 52px;
    }
    span {
      text-indent: 16px;
      font-size: 34px;
      color: rgba(255, 255, 255, 1);
    }
  }
}
</style>



